.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.black-nav {
  background : rebeccapurple;
  width : 100%;
  display : flex;
  justify-content: center;
  color : white;
  padding : 20px;
}
div {
  box-sizing : border-box
}
.list {
  text-align : left;
  padding-left : 20px;
  border-bottom : 1px solid #ddd;
}
.btn-wrap_center {
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}
.btn-wrap_start {
  display: flex;
  gap: 10px;
  justify-content: flex-start;
  align-items: center;
}
.btn-center {
  color: purple;
  background: #ddd;
  border: 1px solid purple;
  width: 160px;
  aspect-ratio: 3/1;
  font-size: 16px;
  margin-top: 20px;
  cursor: pointer;
}
.modal{
  margin-top : 20px;
  padding : 20px;
  background : #eee;
  text-align : left;
}

input#title {
  height: 2.5rem;
  border: 0;
  border-bottom: 1px solid #222;
}
button.btn-submit {
  height: 2.5rem;
  background: mediumpurple;
  color: #fff;
  border: 0;
  width: 4rem;
  display: flex;
  justify-content: center;
  align-items: center;
}